import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGlobe, faIdCard, faBalanceScaleRight, faHandHoldingHeart } from '@fortawesome/free-solid-svg-icons';
import './sass/features.scss';

const Features = () => {
  return (
    <section className={'main__features'}>
      <div>
        <p><FontAwesomeIcon icon={faGlobe} className={'icon'} /></p>
        <h4>Global recruitment</h4>

      </div>
      <div>
        <p><FontAwesomeIcon icon={faBalanceScaleRight} className={'icon'} /></p>
        <h4>International migration law</h4>
      </div>
      <div>
        <p><FontAwesomeIcon icon={faHandHoldingHeart} className={'icon'} /></p>
        <h4>Human rights</h4>
      </div>
      <div>
        <p><FontAwesomeIcon icon={faIdCard} className={'icon'} /></p>
        <h4>Protection and transparensy</h4>
      </div>          
          
    </section>
  )
}

export default Features;
