import React from 'react';
import Features from './Features';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLongArrowAltDown, faLongArrowAltLeft, faLongArrowAltRight } from '@fortawesome/free-solid-svg-icons';
import './sass/main.scss';

const Main = () => {
  return (
    <main>
      <h2 className={'main__title'}>Why JOBLIO<span>?</span></h2>
      <Features />
      <h2 className={'main__title'}>Who <span>We </span>Are?</h2>
      <section className={'section-who'}>
        <div className={'section-who__overlay'}></div>
        <div className={'section-who__text-block'}>
          <p>We are a <span>technology company</span> that is reinventing the process
             of <span>global recruitment</span> of personnel.</p>
          <p>Our technology combined with our professional understanding of 
            <span>international migration laws</span> and deep knowledge of the global recruitment 
            landscape allows employers to efficiently and transparently hire staff 
            from other countries.</p>
          <p>Joblio directly <span>connects employers and employees</span> 
            in circumvention of middlemen and optimizes efficiency of the hiring, 
            processing and relocation of foreign staff.</p>
          <p>Joblio conducts KYC/KYB while bringing efficiency into the ecosystem and ensuring that transparency and 
          <span>human rights</span> are protected. Joblio protects the employer/employee ecosystem 
            by maintaining transparency in a proprietary way.</p>
        </div>
      </section>

      <section className={'section-what'}>
        <h2 className={'main__title'}>What Exactly We <span>Do</span>?</h2>
        <div className={'section-what__text-block'}>
          <div className={'section-what__text-item item1'}>
          <span>We</span> provide legal consulting assistance to both our employer client and 
          the job applicant, from the moment of recruitment, throughout the 
          relocation/migration process, upon arrival at the employer facility and 
          throughout the applicants’ period of employment with the employer.
          </div>
          <div className={'section-what__arrow-item arrow1'}>
            <FontAwesomeIcon icon={faLongArrowAltRight} className={'arrow-icon'}/>
          </div>
          <div className={'section-what__text-item item2'}>
          <span>We</span> ensure that all applicants take a medical exam 48 hours before their 
          transportation to the host country to ensure that they will not pose health 
          risks upon arrival.
          </div>
          <div className={'section-what__arrow-item arrow2'}>
            <FontAwesomeIcon icon={faLongArrowAltDown} className={'arrow-icon'}/>
          </div>
          <div className={'section-what__text-item item3'}>
          <span>We</span> assist newly arrived employees with cultural adaptation and understanding 
          of the host country’s laws and customs.
          </div>
          <div className={'section-what__arrow-item arrow3'}>
            <FontAwesomeIcon icon={faLongArrowAltLeft} className={'arrow-icon'}/>
          </div>
          <div className={'section-what__text-item item4'}>
          <span>We</span>  maintain a hotline, so that newly arrived employees can contact us 
          regarding any questions during the course of their employment.
          </div>
          <div className={'section-what__arrow-item arrow4'}>
            <FontAwesomeIcon icon={faLongArrowAltDown} className={'arrow-icon'}/>
          </div>
          <div className={'section-what__text-item item5'}>
          <span>Our</span>  employer clients have open access to our database of applicants 
          who have gone through the formal KYC process and can always request 
          applicants based on specific criteria.
          </div>
        </div>

      </section>
    </main>
  )
}

export default Main;